import { Box, Typography } from '@mui/material'
import { useTheme } from '@mui/system'
import React from 'react'
import Statistics from './stats/Statistics'
import TrendingNews from './trendingNews/trendingNews'
import fb from '../assets/svgs/fb.svg'
import ig from '../assets/svgs/ig.svg'
import tw from '../assets/svgs/tw.svg'
import In from '../assets/svgs/In.svg'

function Body(props) {
  const { palette } = useTheme()

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        marginTop: '64px',
      }}
    >
      {/* <TrendingNews /> */}
      <Statistics />
      <Box
        style={{
          marginTop: '64px',
          backgroundColor: palette.primary.main,
          color: palette.primary.contrastText,
          padding: '32px',
        }}
      >
        <Typography>
          Welcome to Falsify, the ultimate fact checking WhatsApp bot. Our aim
          is to provide you with accurate and trustworthy information, so that
          you can make informed decisions. In a world where misinformation
          spreads quickly, it's important to be able to distinguish between fact
          and fiction. Say goodbye to misinformation and hello to truth. Use
          Falsify today and never fall for a fake news story again.
        </Typography>
        <Box>
          {[fb, In, tw, ig].map((item) => (
            <img
              style={{
                margin: '20px',
              }}
              src={item}
              alt="social"
            />
          ))}
        </Box>
        <Typography>© 2023 Falsify.org All rights reserved</Typography>
      </Box>
    </div>
  )
}

export default Body
