import axios from "axios";

export async function getNews() {
  let headersList = {
    Accept: "*/*",
    "User-Agent": "Thunder Client (https://www.thunderclient.com)",
  };

  let reqOptions = {
    url: "https://newsdata.io/api/1/news?apikey=pub_17294129453fe785f5340c639ad41e0069eca&language=en&country=za",
    method: "GET",
    headers: headersList,
  };

  let response = await axios.request(reqOptions);
  console.log("GETTING NEWS:", response.data);
  return response.data;
  //   return null;
}
