import React, { useEffect, useState } from 'react'
import {
  Card,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
  Button,
  Divider,
} from '@mui/material'
import NoImagePlaceholder from '../../assets/images/no_image_placeholder.jpg'
import { useTheme } from '@mui/material/styles'

function NewsDisplay(props) {
  const [expandedIndex, setExpandedIndex] = useState(null)
  const { palette } = useTheme()

  const containerStyle = {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-between',
  }

  const cardStyle = {
    width: '30%',
    margin: '1%',
    height: 'auto',
    minWidth: '300px',
    maxWidth: '400px',
    flexGrow: 1,
  }

  const mediaStyle = {
    height: 150,
  }

  const contentStyle = {
    maxHeight: 100,
    overflow: 'hidden',
    textAlign: 'left',
    fontSize: '.8rem',
    marginTop: 10,
    paddingTop: 10,
    borderTop: '1px solid' + palette.primary.main,
    fontStyle: 'italic',
  }

  const handleClick = (index) => {
    setExpandedIndex(index === expandedIndex ? null : index)
  }
  useEffect(()=>{
    console.log('News', props.news)
  },[])

  return (
    <div style={containerStyle}>
      {props.news.map((newsItem, index) => (
        <Card key={newsItem.title} style={cardStyle}>
          <CardActionArea onClick={() => handleClick(index)}>
            {newsItem.image_url ? (
              <CardMedia
                style={mediaStyle}
                image={newsItem.image_url}
                title={newsItem.title}
              />
            ) : (
              <CardMedia
                style={mediaStyle}
                image={NoImagePlaceholder}
                title={newsItem.title}
              />
            )}
            <CardContent>
              <Typography
                gutterBottom
                variant={'h7'}
                style={{
                  color: palette.secondary.main,
                  fontWeight: 'bold',
                  textAlign: 'left !important  ',
                }}
              >
                {newsItem.title}
              </Typography>
              <Typography
                style={contentStyle}
                variant="body2"
                color="textSecondary"
                component="p"
              >
                {newsItem.content.substring(0, 100)}
                {expandedIndex === index
                  ? newsItem.content.substring(100)
                  : '...'}
              </Typography>
              {expandedIndex === index && (
                <Button
                  variant="contained"
                  color="primary"
                  href={newsItem.link}
                  target="_blank"
                >
                  Read More
                </Button>
              )}
            </CardContent>
          </CardActionArea>
        </Card>
      ))}
    </div>
  )
}

export default NewsDisplay
